import React from 'react';
import Notiflix from 'notiflix';
import { useState, useEffect } from 'react';
import { nanoid } from 'nanoid';
import { useGetContactsQuery } from 'redux/contactSlice';
import { useAddContactMutation } from 'redux/contactSlice';
import { Loader } from 'components/Loader/Loader';

import { FormEl, Label, Input, ButtonForm } from './Form.styled';

const Form = () => {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const { data: contacts } = useGetContactsQuery();
  const [addContact, { isLoading, isSuccess }] = useAddContactMutation();

  const numberInputId = nanoid();
  const nameInputId = nanoid();

  useEffect(() => {
    if (isSuccess) {
      Notiflix.Notify.success(`Contact added successfully`);
      reset();
    }
  }, [isSuccess]);

  const inputChange = event => {
    const { name, value } = event.currentTarget;

    switch (name) {
      case 'name':
        setName(value);
        break;
      case 'number':
        setNumber(value);
        break;
      default:
        return;
    }
  };

  const handleSubmit = event => {
    event.preventDefault();
    const newContact = {
      name,
      number,
    };

    const stateName = contacts
      .map(contact => contact.name.toLowerCase())
      .includes(name.toLowerCase());

    if (stateName) {
      Notiflix.Notify.failure(`${name} is already in contacts`);
      return;
    } else {
      addContact(newContact);
    }
  };

  const reset = () => {
    setName('');
    setNumber('');
  };

  return (
    <FormEl onSubmit={handleSubmit}>
      <Label htmlFor={nameInputId}>Name </Label>
      <Input
        type="text"
        name="name"
        id={nameInputId}
        value={name}
        onChange={inputChange}
        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
        title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
        required
      />

      <Label htmlFor={numberInputId}>Phone </Label>
      <Input
        type="tel"
        name="number"
        id={numberInputId}
        value={number}
        onChange={inputChange}
        pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
        title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
        required
      />
      {isLoading ? (
        <Loader />
      ) : (
        <ButtonForm type="submit">Add Contact</ButtonForm>
      )}
    </FormEl>
  );
};

export { Form };
